body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}
body.overlay {
  overflow: hidden;
}

p, ul li {
  font-size: 1.4rem;
}
@media (min-width: 951px) {
  p, ul li {
    font-size: 1.8rem;
  }
}

.container {
  width: 90%;
  margin: 2.5rem auto;
}
@media (min-width: 951px) {
  .container {
    margin: 5rem auto;
  }
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 2.5rem 0;
  font-weight: 600;
  color: #116FB5;
}

