  }
}
@media (min-width: 2001px) {
  h4 {
    font-size: 2.4rem;
  }
}

p {
  font-size: 1.6rem;
}
@media (min-width: 951px) {
  p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1251px) {
  p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1441px) {
  p {
    font-size: 2rem;
  }
}
@media (min-width: 2001px) {
  p {
    font-size: 2.2rem;
  }
}

textarea {
  min-height: 10rem;
}

option, select {
  text-transform: capitalize;
}

.no-display {
  display: none !important;
}

.no-click {
  pointer-events: none;
}

.text-left {
  text-align: left;
  background-color: transparent;
  margin: 0;
  margin-bottom: 4rem;
}

.slider-main {
  margin-top: 4rem;
  position: relative;
}
.slider-main::after {
  clear: both;
  display: table;
  content: "";
}

.bloque-btn {
  display: flex;
  flex-direction: column;
}
.bloque-btn a {
  margin-bottom: 2rem;
}

.disabled {
  pointer-events: none;
}

.disabled-btn {
  background-color: #c5c5c5 !important;
  color: #1e1e1e !important;
  pointer-events: none;
}
.disabled-btn:hover {
  background-color: #c5c5c5 !important;
  color: #1e1e1e !important;
}

.text-yellow {
  color: #F39C3D;
}

.text-blue {
  color: #116FB5;
}

.text-green {
  color: #A6C84E;
}

.m-t-0 {
  margin-top: 0;
}

.m-b-0 {
  margin-bottom: 0;
}

.m-t-1 {
  margin-top: 1rem;
}

.m-b-1 {
  margin-bottom: 1rem;
}

.m-t-5 {
  margin-top: 2.5rem !important;
}
@media (min-width: 951px) {
  .m-t-5 {
    margin-top: 5rem !important;
  }
}

.m-b-5 {
  margin-bottom: 5rem;
}

@media (max-width: 950px) {
  .m-t-5--only-mobile {
    margin-top: 5rem;
  }
}

@media (max-width: 950px) {
  .m-b-5--only-mobile {
    margin-bottom: 5rem;
  }
}

.center {
  text-align: center;
}

.text-yellow {
  color: #F39C3D;
}

.text-green {
  color: #A6C84E;
}

.text-blue {
  color: #116FB5;
}

.text-white {
  color: #FFFFFF;
}

.text-black {
  color: #0a0a0a;
}

.text-gray {
  color: #5e5e5e;
}

.text--pink {
  color: #CD214F;
}

.text--orange {
  color: #ED7827;
}

.text-20 {
  font-size: 1.4rem !important;
}
@media (min-width: 951px) {
  .text-20 {
    font-size: 1.8rem !important;
  }
}
@media (min-width: 1251px) {
  .text-20 {
    font-size: 2rem !important;
  }
}

.text-24 {
  font-size: 1.6rem !important;
}
@media (min-width: 951px) {
  .text-24 {
    font-size: 2rem !important;
  }
}
@media (min-width: 1251px) {
  .text-24 {
