  }
}

.caps {
  text-transform: capitalize;
}

.title-info {
  font-size: 2.2rem;
}
@media (min-width: 951px) {
  .title-info {
    font-size: 2.5rem;
  }
}

.caps {
  text-transform: capitalize;
}

.overlay {
  overflow: hidden !important;
  overflow-y: hidden !important;
}

@media (min-width: 951px) {
  .flex {
    display: flex;
    gap: 2rem;
  }
}

@media (min-width: 951px) {
  .flex--gap-3 {
    display: flex;
    gap: 3rem;
  }
}

@media (min-width: 951px) {
  .flex--gap-5 {
    display: flex;
    gap: 5rem;
  }
}

.flex-icon {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.block {
  display: block;
  width: 100%;
}

.deleteModal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  overflow: auto;
}
.deleteModal__modal {
  margin: 5rem 0;
  background-color: #353535;
  padding: 3rem;
  width: 90%;
  border-radius: 1rem;
  border-width: 0.2rem;
  border-color: #F39C3D;
  border-style: solid;
  position: relative;
  z-index: 11;
}
@media (min-width: 951px) {
  .deleteModal__modal {
    width: 40%;
  }
}
.deleteModal__icon {
  color: #F39C3D;
  text-align: center;
  font-size: 2rem;
}
@media (min-width: 951px) {
  .deleteModal__icon {
    font-size: 3rem;
  }
}
.deleteModal__paragraph {
  margin-top: 0.5rem;
}
.deleteModal__btn-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: #F39C3D;
  font-size: 2.6rem;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 951px) {
  .deleteModal__btn-close {
    font-size: 4rem;
  }
}

@media (min-width: 951px) {
  .only--mobile {
    display: none;
  }
}

.only--desktop {
  display: none !important;
}
@media (min-width: 951px) {
  .only--desktop {
    display: block !important;
  }
}

.header {
  background-color: #116FB5;
  color: #FFFFFF;
  padding: 1rem;
  text-align: center;
}
@media (min-width: 951px) {
  .header {
    padding: 2rem 5rem;
  }
}
.header__bar {
  display: flex;
  justify-content: space-between;
}
.header__logo {
  width: 24rem;
}
@media (min-width: 951px) {
  .header__logo {
    width: 30rem;
  }
}
.header__right {
  display: flex;
  align-items: center;
}
@media (min-width: 951px) {
  .header__right {
    width: 50%;
  }
}
.header__lang {
  background: url("../img/lang.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: flex;
  justify-content: center;
  position: relative;
  min-width: 5rem;
  height: 2.5rem;
  z-index: 11;
}
.header__lang:hover {
  cursor: pointer;
}
@media (max-width: 950px) {
  .header__lang {
    justify-content: flex-end;
    background-position: 100%;
    grid-column: 1/2;
    margin-top: 1.5rem;
    margin-right: 3rem;
  }
}
@media (max-width: 565px) {
  .header__lang {
    margin-right: 0;
    margin-top: 0;
    transform: translateX(20%);
  }
}
@media (min-width: 1251px) {
  .header__lang {
    height: 3rem;
  }
}
.header__lang--btn {
  background-color: #151A2E;
  border: none;
  color: #FFFFFF;
  font-size: 1.8rem;
  padding: 0.5rem 1rem;
}
.header__lang--btn:hover {
  cursor: pointer;
  background-color: #60C0C5;
  color: #151A2E;
}
.header__select {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 100%;
  justify-content: center;
  margin-top: 1rem;
  background-color: #151A2E;
  border-radius: 0.1rem;
  padding: 1rem;
}
@media (min-width: 951px) {
  .header__select {
    min-width: 10rem;
    row-gap: 1rem;
  }
}
.header__nav {
  width: 100%;
}
.header__nav--link {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.6rem;
}
@media (min-width: 951px) {
  .header__nav--link {
    font-size: 2.2rem;
  }
}
.header__nav--link:hover {
  color: #151A2E;
}
.header__nav--ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}
@media (min-width: 951px) {
  .header__nav--ul {
    justify-content: space-evenly;
  }
}